html, body, div, span, h1, h2, h3, h4, h5, h6, p, a, img, ul, li, fieldset, form, article, footer, header, menu, nav, section {
  border: 0;
  margin: 0;
  padding: 0;
  font-family: inherit;
}

ol, ul {
  list-style: none;
}

@font-face {
  font-family: Roboto, Arial, Helvetica, sans-serif;
  src: url("Roboto-Regular-webfont.55e67705.woff") format("woff");
  font-weight: normal;
  font-style: normal;
}

.page {
  scroll-behavior: smooth;
  box-sizing: border-box;
  font-family: Open Sans, sans-serif;
  font-size: 14px;
  font-weight: 400;
}

.page__body {
  background: #f2f6fa;
}

.page__body--with-menu {
  overflow: hidden;
}

.page__body-header {
  z-index: 1;
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
}

*, :before, :after {
  box-sizing: border-box;
}

.grid {
  grid-template-columns: repeat(12, 1fr);
  gap: 30px;
  display: grid;
}

@media (width >= 640px) and (width <= 1023px) {
  .grid {
    grid-template-columns: repeat(6, 1fr);
    gap: 30px;
  }
}

@media (width >= 1024px) and (width <= 1279px) {
  .grid {
    grid-template-columns: repeat(12, 1fr);
    gap: 20px;
  }
}

.grid--responsive {
  display: grid;
}

@media (width >= 320px) and (width <= 639px) {
  .grid--responsive {
    display: block;
  }
}

